let id = '';
let timer;
let heightArr = [];
let ctime = parseInt(Date.now());
let offset = 0,
	total = 10;
import {
	getImgsrc
} from '@/common/util/index.js';
import uParse from '@/components/un-parse/u-parse.vue'; //由于插件上传命名问题在目录上加了一个n
export default {
	components: {
		uParse
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {
			// 来自页面内分享按钮
			console.log(res.target);
		}
		this.addArticleCountNum('forwardNum')
		return {
			title: this.info.title,
			path: '/pages/home/index/index?articleId=' + id,
			imageUrl: this.info.attachment[0].url
		};
	},
	data() {
		return {
			info: {},
			richTextHeight: 0,
			isShowMore: false,
			articleHeight: 0,
			parames: {},
			scrollIntoId: '',
			old: {
				scrollTop: 0
			},
			commentList: [],
			loadingType: 0,
			loadingText: {
				contentdown: "",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多评论了"
			},
		};
	},
	onLoad(options) {
		id = options.id;
		console.log('id============' + id);
		this.parames = {
			articleId: id,
			target: 'articleDetail'
		};
		this.parames = JSON.stringify(this.parames);
		this.init();
		this.addArticleCountNum('clickNum')
		// setTimeout(() => {
		// 	this.richTextHeight = Math.max.apply(null,heightArr)
		// 	clearInterval(timer)
		// }, 5000)
	},
	onUnload() {
		offset = 0;
	},
	// async onReady() {
	// 	timer = setInterval(async () => {
	// 		var size = await this.getElSize('fake-rich')
	// 		heightArr.push(size.height)
	// 		console.log(Math.max.apply(null,heightArr))
	// 		if (Math.max.apply(null,heightArr) >= this.screenHeight * 2) {
	// 			this.richTextHeight = this.screenHeight * 2
	// 			this.isShowMore = true
	// 		} else {
	// 			this.richTextHeight = Math.max.apply(null,heightArr)
	// 			this.isShowMore = false
	// 		}
	// 	}, 100)
	// },
	methods: {
		init() {
			uni.showLoading({
				title: "加载中"
			})
			this.api.home.article.get_detail({
					article_id: id,
					request_type: 'h5'
				},
				res => {
					console.log(res.data);
					this.info = res.data;
					this.api.home.article.get_comment_list({
						ctime,
						articleId: id,
						offset,
						total
					}, res => {
						console.log(res)
						this.commentList = res.data
						uni.hideLoading()
					});
				}
			);
		},
		addArticleCountNum(type) {
			this.api.home.article.add_count({
				articleId: id,
				type
			}, res => {
				console.log(res)
			})
		},
		getMoreComment() {
			offset += total
			this.api.home.article.get_comment_list({
				ctime,
				articleId: id,
				offset,
				total
			}, res => {
				console.log(res)
				if (res.data.length) {
					this.commentList = this.commentList.concat(res.data)
					this.loadingType = 0
				} else {
					this.loadingType = 2
				}
			});
		},
		scroll() {
			this.scrollIntoId = '';
		},
		scrollIntoComment() {
			this.scrollIntoId = 'comments';
			console.log(this.scrollIntoId);
		},
		getElSize(id) {
			//得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery()
					.select('#' + id)
					.fields({
							size: true,
							scrollOffset: true,
							rect: true
						},
						data => {
							res(data);
						}
					)
					.exec();
			});
		},
		goFamous() {
			uni.navigateTo({
				url: '../../celebrity/detail/detail?id=' + this.info.userId
			});
		},
		goCategory() {
			uni.navigateTo({
				url: '../../../classify/detail/detail?id=' + this.info.categoryId
			});
		},
		preview() {},
		toggleCollect() {
			console.log('id============' + id);
			this.api.home.article.toggle_collect({
					articleId: id,
					action: this.info.faFlag ? 0 : 1
				},
				res => {
					this.info.faFlag = !this.info.faFlag;
					if (this.info.faFlag) {
						uni.showToast({
							title: "收藏成功"
						})
					} else {
						uni.showToast({
							title: "取消收藏"
						})
					}
				}
			);
		},
		togglePraise() {
			console.log('id============' + id);
			this.api.home.article.toggle_praise({
					articleId: id,
					action: this.info.praiseFlag ? 0 : 1
				},
				res => {
					this.info.praiseFlag = !this.info.praiseFlag;
					if (this.info.praiseFlag) {
						uni.showToast({
							title: "点赞成功"
						})
					} else {
						uni.showToast({
							title: "取消点赞"
						})
					}
				}
			);
		},
		toggleCommentPraise(id, i) {
			this.api.home.comment.toggle_praise({
					commentId: id,
					action: this.commentList[i].praiseFlag ? 0 : 1
				},
				res => {
					this.commentList[i].praiseFlag = !this.commentList[i].praiseFlag;
					if (this.commentList[i].praiseFlag) {
						this.commentList[i].praiseNum++
						uni.showToast({
							title: "点赞成功"
						})
					} else {
						this.commentList[i].praiseNum--
						uni.showToast({
							title: "取消点赞"
						})
					}
				}
			);
		},

		scrolltolower() {
			if (this.loadingType != 0) {
				return
			}
			this.loadingType = 1
			this.getMoreComment();
		},
		launchAppError(e) {
			console.log(e)
			uni.showModal({
				title: '提示',
				content: '更多功能，请下载“宝宝贝”APP',
				showCancel: false,
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
		// showAll() {
		// 	clearInterval(timer)
		// 	this.richTextHeight = Math.max.apply(null,heightArr)
		// 	this.isShowMore = false
		// }
	}
};
